

/* Introduction CSS Script */
.navbar  /* background-image: linear-gradient(0.25turn, #fafafa, #53a5fc); */
{
  background-color: rgba(23, 31, 62, 0.7); /* Adjust the opacity by changing the fourth parameter (0.7 in this case) */
  height: 75px;
  font-size: 18pt;
  cursor: pointer;
  pointer-events: inherit;
  /* pointer-events: none;  */
  caret-color: transparent;
}

.bg-navbar { 
  background: #403D58;

}


ul {
  list-style-type: square; /* Removes default bullet points */
  padding: 0; /* Removes default padding */
  margin: 0; /* Removes default margin */
}

ul li {
  text-align: left; /* Aligns text within <li> elements to the left */
  font-size: large;

}
