.background-video {
  position: fixed;
  right: 0;
  bottom: 0;
  width: 100%;
  opacity: 0.09;
  height: fit-content;
  z-index: 1;
  /* animation-play-state: running; */
}

#container{ 
  width: 99%;
  align-items: center;
  align-self: center;
  align-content: center;
  font-family: 'Times New Roman', Times, serif;
  padding-bottom: 100px;
}

#hoverable{ 
  z-index: 1;
}
.profilephoto{ 

  margin-top: 100pt;
  display: inline-block;
  justify-content: center;
  align-items: center;
  width: 200px; /* Adjust the width and height as needed */
  height: 200px;
  border-radius: 50%; /* Make it circular */
  overflow: hidden;
  /* margin-left: 20%; */
  z-index: 1;
}
.profilephoto img{
  width: 100%;
}

.intro{

display: inline-block;
justify-content: center;
/* margin-left: 20%; */
height: fit-content;
font-family: 'Times New Roman', Times, serif;
font-size: 16pt;
text-align: center;
}


#introbackground {
  background-image: linear-gradient(0.5turn, #53a5fc, #fafafa);

  }


.customcard {
    padding: 20px;
    margin: 30px;
    width: 300px;
    border-radius: 50px;
    box-shadow: 5px 0px 25px 0px black;
    height: 400px;
    align-items: center;
    align-content: center;
    background-color: rgb(254, 254, 254);
    /* font-family: Verdana, Geneva, Tahoma, sans-serif; */
    font-family: 'Times New Roman', Times, serif;
    
  }


.card-title{ 
  border-radius: 5px;
  border-color: black;
  border-width: 1px;


}

.card-footer{
  text-decoration: underline;
  font-style: italic;
}

.customcard:hover {
    transform: scale(1.05);
    cursor: -moz-grab;
    box-shadow: 0px 12px 30px 0px rgba(0, 0, 0, 0.2);
    transition: all 1800ms cubic-bezier(0.19, 1, 0.22, 1);
    
  }


.clogo {
    height: 175px;
    margin-top: 20px;
    margin-bottom: 20px;
  }

.researchcard{
  padding: 20px;
  /* margin: 30px; */
  margin-bottom: 50px;
  display: inline-block;
  position: relative;
  width: 500px;
  border-radius: 50px;
  box-shadow: 5px 0px 25px 0px black;
  height: 300px;
  background-color: rgb(254, 254, 254);
  font-family: 'Times New Roman', Times, serif;
  
  


  z-index: 1;
}

.projectcard{ 

  /* margin-top: 10%; */
  /* margin-left: 15%; */
  padding: 20px;
  margin-bottom: 50px;
  width: 600px;
  height: 300px;
  border-radius: 20pt;
  background-color: white;
  box-shadow: 5px 0px 25px 0px black;

}

.projectcard img{

  width: 70%;
  height:70%;
}

.hline {
  position: relative;
  width: 100%;
  height: 2px;
  background-color: black;
  /* background: rgba(0, 0, 0, 0.1); */
  filter: blur(1px);
  z-index: 10;
}

